}

.cta-txt {
    font-weight: 400;
    font-size: 1.2rem;
    color: white;
    width: 100%;
}

.cta:hover {
    -webkit-transform: scaleX(1.03);
            transform: scaleX(1.03);
}

.reports-header-container {
    /* margin-top: 3rem; */
    padding: .7rem;
    /* width: calc(50% - 1.4rem); */
    color: white;
    font-family: Roboto;
    /* max-height: 200px; */
